$primary-color: #0790e8;
$primary-color-hover: #0373ba;

$secondary-text-color: #747474;

$warning-notice-background-color: #fff3cf;
$warning-notice-font-color: #000;
$error-notice-background-color: #f9e2e2;
$error-notice-font-color: #000;
$error-font-color: #d9534f;
$info-notice-background-color: #d2eaff;
$info-notice-font-color: #000;
$success-color: #6bc45a;

$font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$font-size: 15px;
$line-height: 1.5;

$border-radius: 5px;

$space-between-boxes: 20px;

$sidebar-width: 250px;

$box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);

$section-padding: 25px;

$section-contents-spacing-between-vertical: 10px;
$section-contents-spacing-between-horizontal: 15px;

$section-title-font-size: 18px;
$section-title-font-weight: 600;

$section-field-padding: 8px 12px;
$section-field-height: 37px;
$section-field-transition: all 150ms ease-in-out;
$section-field-border: 1px solid #cdcdcd;

$section-select2-multiple-padding: 4px 12px;

$section-subsection-border: 1px solid #ccd0d4;

$section-notice-padding: 10px 15px;

$section-tag-padding: 4px 10px;
$section-tag-font-size: 13px;

$generate-button-padding: 6px 15px;
$generate-button-font-size: 14px;

$code-preview-background: #f1f1f1;
$code-preview-padding: 25px;