.uoc-generate {
	display: grid;
	grid-template-columns: 1fr $sidebar-width;
	grid-template-rows: 1fr;
	gap: $space-between-boxes $space-between-boxes;
	grid-template-areas: '. .';

	font-family: $font-family;
	font-size: $font-size;
	line-height: $line-height;

	@media screen and (max-width: 1024px){
		grid-template-columns: 1fr;
	}

	&-hidden {
		display: none;
	}

	&-required-asterisk {
		color: red;

		font-size: 13px;
		vertical-align: super;

		&:before {
			content: '*';
		}
	}

	&-button {
		padding: $generate-button-padding; 
		font-size: $generate-button-font-size; 
		border-radius: $border-radius;
		box-shadow: $box-shadow;
		line-height: $line-height;

		outline: none;
		display: inline-block;

		position: relative;

		font-weight: 500;

		text-align: center;
		/* white-space: nowrap; */
		vertical-align: middle;

		text-align: left;

		user-select: none;

		color: #000;
		border: 1px solid #000;
		background-color: transparent;

		text-decoration: none;

		transition: color 150ms ease-in-out,
		            background-color 150ms ease-in-out,
		            border-color 150ms ease-in-out, 
		            box-shadow 150ms ease-in-out;

		cursor: pointer;

		&:hover {
			background: #000;
			color: #fff;
		}

		&--primary {
			border-color: $primary-color;
			color: $primary-color;

			&:hover {
				background: $primary-color;
				color: #fff;
			}
		}
	}

	&-tag {
		padding: $section-tag-padding;

		font-size: $section-tag-font-size;
		font-weight: 500;

		border-radius: $border-radius;

		margin-left: 5px;

		line-height: 1;

		display: inline-block;

		&--warning {
			background: $warning-notice-background-color;
			color: $warning-notice-font-color;
		}

		&--error {
			background: $error-notice-background-color;
			color: $error-notice-font-color;
		}

		&--info {
			background: $info-notice-background-color;
			color: $info-notice-font-color;
		}
	}

	&-notice {
		padding: $section-notice-padding;

		border-radius: $border-radius;

		margin-top: $section-contents-spacing-between-vertical;

		&--invert-margin {
			margin-top: 0;
			margin-bottom: $section-contents-spacing-between-vertical;
		}

		&--warning {
			background: $warning-notice-background-color;
			color: $warning-notice-font-color;
		}

		&--error {
			background: $error-notice-background-color;
			color: $error-notice-font-color;
		}

		&--info {
			background: $info-notice-background-color;
			color: $info-notice-font-color;
		}

		&__title {
			font-size: 16px;
			font-weight: 600;
		}

		&__action {
			padding-top: $section-contents-spacing-between-vertical;
		}
	}

	&-section {
		background: #fff;

		padding: $section-padding;
		border-radius: $border-radius;

		margin-top: $space-between-boxes;

		&__title {
			font-size: $section-title-font-size;
			font-weight: $section-title-font-weight;

			margin-bottom: $section-contents-spacing-between-vertical;
		}

		&__description {
			margin-bottom: $section-contents-spacing-between-vertical;
		}

		&-form {
			&-field {
				display: block;
				position: relative;

				&:not(:last-child) {
					padding-bottom: $section-contents-spacing-between-vertical * 1.5;
				}

				&:before {
					content: '';

					width: 16px;
					height: 16px;

					background-size: contain;
					background-repeat: no-repeat;
					background-position: center;

					position: absolute;
					right: 10px;
					top: 10px;
				}

				&--loading:before {
					background-image: url( '../img/spinning-line.svg' );
				}

				&--success:before {
					background-image: url( '../img/check-green.svg' );
				}

				&--success {
					input {
						border-color: $success-color !important;
					}
				}

				&-row {
					@media screen and (max-width: 800px){
						padding-bottom: $section-contents-spacing-between-vertical * 1.5;
					}
				}

				&__label {
					font-weight: 600;
					padding-bottom: $section-contents-spacing-between-vertical;
				}

				&__error:not(:empty) {
					color: $error-font-color;
					padding-top: $section-contents-spacing-between-vertical;
				}

				&__description {
					color: $secondary-text-color;
					padding-top: $section-contents-spacing-between-vertical;

					&--top {
						padding-top: 0;
						padding-bottom: $section-contents-spacing-between-vertical;
					}
				}

				input[type="text"],
				input[type="number"],
				input[type="date"],
				input[type="time"],
				&__text,
				textarea,
				&__textarea {
					width: 100%;

					height: $section-field-height;
					transition: $section-field-transition;

					padding: $section-field-padding;

					border: $section-field-border;
					box-shadow: $box-shadow;

					outline: none;

					&[disabled],
					&[readonly] {
						background: #f0f0f1;
					}
				}

				textarea,
				&__textarea {
					height: auto;
				}

				select,
				&__select {
					width: 100% !important;
				}

				&__radio,
				&__checkbox {
					display: flex;

					margin-bottom: -5px;

					input[type="radio"],
					input[type="checkbox"] {
						flex: 0 0 auto;

						margin-top: 3px;
					}

					&-description {
						padding-left: calc( $section-contents-spacing-between-horizontal / 2 );

						&-title {
							font-weight: 600;
						}
					}
				}
			}

			&-row {
				&--half {
					display: grid;
					grid-template-columns: 1fr 1fr;
					grid-template-rows: 1fr;
					gap: $space-between-boxes $space-between-boxes;
					grid-template-areas:
					". .";

					@media screen and (max-width: 800px){
						grid-template-columns: 1fr;
					}
				}
			}
		}	
	}

	&-subsection {
		margin-top: $section-padding;
		padding-top: $section-padding;
		border-top: $section-subsection-border;
	}

	&-dependency {
		&-option {
			width: 100%;
			display: flex;

			&:first-child {
				padding-top: $section-contents-spacing-between-vertical;
			}

			&:not(:first-child){
				padding-top: $section-contents-spacing-between-vertical * 2;
			}

			&__checkbox {
				flex: 0 0 auto;

				padding-right: $section-contents-spacing-between-horizontal;

				// This padding top is used to create the effect that the 
				// radio field is aligned vertically with the image
				padding-top: 14px;
			}

			&__icon {
				width: 50px;
				height: 50px;

				flex: 0 0 auto;

				background-repeat: no-repeat;
				background-size: contain;
				background-position: center;

				&--learndash {
					background-image: url( '../img/dependency-learndash.svg' );
				}

				&--automator {
					background-image: url( '../img/dependency-automator.svg' );
				}
			}

			&__content {
				padding-left: $section-contents-spacing-between-horizontal;
			}

			&__title {
				font-size: 16px;
				font-weight: 600;
			}
		}
	}

	&-code-preview {
		background: $code-preview-background;
		padding: $code-preview-padding;

		border-radius: $border-radius;
		margin-top: $section-contents-spacing-between-vertical;

		text-align: center;

		&__title {
			font-weight: 600;
		}

		&__code {
			font-family: monospace;
			letter-spacing: 2px;
			font-size: 18px;
			font-weight: 400;
			word-break: break-all;

			margin-top: $section-contents-spacing-between-vertical;
		}
	}

	&-main {}

	&-sidebar {}

	&-batch-name {}
}